@import url(https://fonts.googleapis.com/css2?family=Alkatra:wght@500&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Righteous&display=swap);
body {
  background: rgb(0,0,0);
    background: linear-gradient(0deg, rgba(0,0,0,1) 16%, rgba(38,0,51,1) 39%, rgba(109,0,115,1) 79%, rgba(183,213,229,1) 100%);
}

*{
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

ul{
  list-style: none;
}

a{
  text-decoration: none;
}
.containerCarousel{
    display: flex;
}

.carousel{
    display: flex;
    height: 50vw;
    justify-content: flex-start;
    align-items: center;
    padding-left: 20px;
    grid-gap: 20px;
    gap: 20px;
    
}

.imageCarousel{
    height: 30px;
    width: 20px;
    cursor: pointer;
    transition: 0.2s ease-in-out;
}

.imageCarousel:hover{
    -webkit-animation: arrowAnimation 1s infinite;
            animation: arrowAnimation 1s infinite;
}

.imageCarousel.next{
    top: 20px;
    right: 30vw;
}

.imageCarousel.previous{
    bottom: 10px;
}

.carousel span{
    font-size: 30px;
}

.carousel .contents{
    height: 21vw;
    width: 50vw;
    display: flex;
    align-items: center;
    grid-gap: 20px;
    gap: 20px;
    padding: 10px;
    border-radius: 5%;
    border: 3px dashed #470052;

    background-color: #CF01FF;
}

.carousel .imageProject{
    width: 300px;
    height: 200px;
    border: 2px solid bisque;
    border-radius: 10px;
    cursor: pointer;
}

.carousel .imageProject:hover{
    -webkit-animation: projectAnimation 2s infinite;
            animation: projectAnimation 2s infinite;
}

.carousel .contents .description{
    font-family: Alkatra;
    text-align: justify;
    display: flex;
    flex-direction: column;
    padding-right: 10px;
    line-height: 1.2;
    grid-gap: 10px;
    gap: 10px;
}

.containerCarousel .observerImage{
    height: 28em;
    width: 33em;
}


.containerDialog .dialogBoxCarousel{
    display: flex;
    align-items: center;
    justify-content: center;
    -webkit-transform: translate(-40%, 25%);
            transform: translate(-40%, 25%);
}


.dialogBoxCarousel .dialogBox{
    -webkit-transform: scaleX(-1);
            transform: scaleX(-1);
    height: 10em;
    width: 30em;
}


@-webkit-keyframes arrowAnimation {
    0%{
        -webkit-transform: scale(1.0);
                transform: scale(1.0);
    }
    50%{
        -webkit-transform: scale(1.2);
                transform: scale(1.2);
    }
    100%{
        -webkit-transform: scale(1.0);
                transform: scale(1.0);
    }
}


@keyframes arrowAnimation {
    0%{
        -webkit-transform: scale(1.0);
                transform: scale(1.0);
    }
    50%{
        -webkit-transform: scale(1.2);
                transform: scale(1.2);
    }
    100%{
        -webkit-transform: scale(1.0);
                transform: scale(1.0);
    }
}

@-webkit-keyframes projectAnimation {
    0%{
        -webkit-transform: scale(1.0);
                transform: scale(1.0);
    }
    50%{
        -webkit-transform: scale(1.03);
                transform: scale(1.03);
    }
    100%{
        -webkit-transform: scale(1.0);
                transform: scale(1.0);
    }
}

@keyframes projectAnimation {
    0%{
        -webkit-transform: scale(1.0);
                transform: scale(1.0);
    }
    50%{
        -webkit-transform: scale(1.03);
                transform: scale(1.03);
    }
    100%{
        -webkit-transform: scale(1.0);
                transform: scale(1.0);
    }
}









.imagesTec .tecnology img{
    height: 50px;
    width: 50px;
}

.containerTec{
    display: flex;
    height: 90vh;
}


.containerTec .observe{
    width: 400px;
    height: 400px;
}

.containerTec .dialogBoxTec{
    display: flex;
    justify-content: center;
    -webkit-transform: translate(25%, 35%);
            transform: translate(25%, 35%);
}

.containerTec .dialogBoxTec img{
    height: 10em;
    width: 30em;
}

.containerTec .dialogBoxTec p{
    height: 6em;
    width: 26em;
    text-align: justify;
    margin-top: 1.0em;
    overflow-y: auto;
}

.carouselTec .tecnology{
    display: flex;
    flex-direction: column;
    padding-left: 40px;
    align-items: center;
    grid-gap: 5px;
    gap: 5px; 
    transition: 0.2s ease-in-out;
}

.carouselTec{
    height: -webkit-fit-content;
    height: -moz-fit-content;
    height: fit-content;
    width:  -webkit-fit-content;
    width:  -moz-fit-content;
    width:  fit-content;
    display: grid;
    grid-template-columns: 1fr 1fr 1fr 1fr;
    grid-auto-rows: 5em;
    margin: 50px auto;
}


.carouselTec::-webkit-scrollbar{
    display: none;
}

.carouselTec .tecnology span{
    white-space: nowrap;
    font-size: 12px;
}

.carouselTec .tecnology:hover{
    -webkit-transform: scale(1.1);
            transform: scale(1.1);
    cursor: pointer;
}

