p{
    position: absolute;
}

.apresentationImage{
    display: flex;
    justify-content: flex-end;

}

.dialogBoxStart{
    display: flex;
    align-items: center;
    justify-content: center;
    -webkit-transform: translate(40%, -25%);
            transform: translate(40%, -25%);
}

.dialogBoxStart .dialogBox{
    -webkit-transform: scaleX(-1);
            transform: scaleX(-1);
    height: 10em;
    width: 30em;
}

.apresentationText{
    width: 25em;
    height: 6em;
    margin-bottom: 2em;
    padding-right: 8px;
    text-align: justify;
