}


.sideMenu{
    position: fixed;
    top: 0;
    right: 0;
    width: 4em;
    display: flex;
    justify-content: center;
    align-items: center;
    min-height: 100vh;
}

.sideMenu .icon{
    width: 30px;
    -webkit-filter: invert(1);
            filter: invert(1);
    transition: 0.2s ease-in-out;
}

.sideMenu .icon:hover{
    -webkit-transform: scale(1.2);
            transform: scale(1.2);
}

.sideMenu ul{
    display: flex;
    flex-direction: column;
    list-style-type: none;
    grid-gap: 2em;
    gap: 2em;
}





